<template>
    <div class="m-publish-box">
        <!-- Â§¥ÈÉ® -->
        <publish-header name="ÊñáÈõÜÂ∞èÂÜå" :localDraft="false">
            <slot name="header"></slot>
        </publish-header>

        <el-form label-position="left" class="m-publish-collection">
            <!-- üíõ Ê†èÁõÆÂ≠óÊÆµ -->
            <div class="m-publish-title">
                <el-divider content-position="left">Ê†áÈ¢ò</el-divider>
                <el-input
                    v-model="collection.title"
                    placeholder="ËØ∑ËæìÂÖ•Â∞èÂÜåÊ†áÈ¢ò"
                    maxlength="30"
                    show-word-limit
                ></el-input>
            </div>

            <div class="m-publish-primary">
                <div class="m-publish-primary-block">
                    <el-divider content-position="left">ÂèØËßÅÊÄß</el-divider>
                    <el-radio v-model.number="collection.public" :label="this.public.PUBLIC">ÂÖ¨ÂºÄ</el-radio>
                    <el-radio v-model.number="collection.public" :label="this.public.PRIVATE">ÁßÅÊúâ</el-radio>
                </div>
                <div class="m-publish-primary-block">
                    <el-divider content-position="left">Â∞ÅÈù¢Âõæ</el-divider>
                    <publish-banner v-model="collection.image"></publish-banner>
                </div>
                <div class="m-publish-primary-block m-publish-collection-posts">
                    <el-divider content-position="left">ÂÜÖÂÆπ</el-divider>
                    <draggable
                        class="m-list-style"
                        tag="ul"
                        v-if="collection.posts && collection.posts.length"
                        :list="collection.posts"
                        handle=".u-move"
                    >
                        <li v-for="(item, key) in collection.posts" :key="key" class="c-posts-item">
                            <i class="u-move el-icon-more"></i>
                            <i
                                class="u-delete el-icon-close"
                                @click="collection.posts.splice(key, 1)"
                            ></i>
                            <el-row class="m-posts-item" :gutter="10">
                                <el-col :span="4" class="u-collection-type">
                                    <el-select
                                        class="u-item-key"
                                        v-model="item.type"
                                        placeholder="ËØ∑ÈÄâÊã©ÊñáÁ´†Á±ªÂûã"
                                        @change="
                                            () => {
                                                search_handle(null, item);
                                                item.url = item.title = '';
                                            }
                                        "
                                    >
                                        <el-option
                                            v-for="(type, k) in source_types"
                                            :label="type"
                                            :key="k"
                                            :value="k"
                                        ></el-option>
                                    </el-select>
                                </el-col>
                                <el-col :span="8" class="u-collection-id">
                                    <el-select
                                        v-if="item.type !== 'custom'"
                                        class="u-item-value"
                                        v-model="item.id"
                                        filterable
                                        remote
                                        placeholder="ÈÄöËøáËæìÂÖ•ÊñáÁ´†Ê†áÈ¢òËøõË°åÊêúÁ¥¢"
                                        :remote-method="
                                            (query) => {
                                                search_handle(query, item);
                                            }
                                        "
                                        @change="
                                            (post_id) => {
                                                title_fill(post_id, item);
                                            }
                                        "
                                    >
                                        <template v-for="post in item.posts">
                                            <el-option
                                                v-if="post.id && post.title"
                                                :key="post.id"
                                                :value="post.id"
                                                :label="post.title"
                                            ></el-option>
                                        </template>
                                    </el-select>
                                    <el-input
                                        class="u-item-value"
                                        placeholder="ËØ∑ËæìÂÖ•ÂÆåÊï¥ÁΩëÈ°µÈìæÊé•ÔºàÈúÄ‰ª•HTTPÊàñHTTPSÂºÄÂ§¥Ôºâ"
                                        v-else
                                        v-model="item.url"
                                    ></el-input>
                                </el-col>
                                <el-col :span="12" class="u-collection-url" v-if="item.url">
                                    <el-input v-model="item.title" placeholder="ËØ∑ËæìÂÖ•Ëá™ÂÆö‰πâÊ†áÈ¢ò"></el-input>
                                </el-col>
                            </el-row>
                        </li>
                    </draggable>
                    <div v-else class="u-posts-items-empty">ÊöÇÊó†ÊñáÁ´†‰ø°ÊÅØÔºåËØ∑ËøõË°åÊñáÁ´†Ê∑ªÂä†</div>
                    <div class="u-posts-add" @click="add_posts_item">
                        <i class="el-icon-plus"></i>
                        <span>Ê∑ªÂä†ÊñáÁ´†</span>
                    </div>
                </div>

                <div class="m-publish-primary-block m-publish-description">
                    <el-divider
                        content-position="left"
                        @click="show_description = !show_description"
                    >ÊèèËø∞ÔºàÈÄâÂ°´Ôºâ</el-divider>
                    <span
                        v-if="!show_description"
                        @click="show_description = true"
                        class="u-show"
                    >‚ñº Â±ïÂºÄ</span>
                    <span
                        v-if="show_description"
                        @click="show_description = false"
                        class="u-hide"
                    >‚ñ≤ Êî∂Ëµ∑</span>
                    <Tinymce
                        v-show="show_description"
                        v-model="collection.description"
                        :attachmentEnable="true"
                        :resourceEnable="true"
                        :height="300"
                    />
                </div>

                <div class="m-publish-primary-block m-publish-tags">
                    <el-divider content-position="left">Ê†áÁ≠æÔºàÈÄâÂ°´Ôºâ</el-divider>
                    <ul class="m-list-style">
                        <li v-for="(t, key) in collection.tags" :key="key" class="m-tag">
                            <span v-text="t"></span>
                            <i class="el-icon-close" @click="collection.tags.splice(key, 1)"></i>
                        </li>
                    </ul>
                    <div class="m-tag-add" v-if="collection.tags.length < 5">
                        <el-autocomplete
                            class="u-tag-add-value"
                            placeholder="ËØ∑ËæìÂÖ•Ê†áÁ≠æÔºàÊúÄÂ§ö‰∏∫5‰∏™Ôºâ"
                            v-model="tag"
                            :fetch-suggestions="tags_search"
                        ></el-autocomplete>
                        <el-button
                            class="u-tag-add"
                            @click="
                                () => {
                                    if (tag) collection.tags.push(tag);
                                    tag = '';
                                }
                            "
                        >
                            <i class="el-icon-plus"></i>
                        </el-button>
                    </div>
                </div>
            </div>

            <el-form-item>
                <el-button
                    class="m-publish-collection-publish u-publish"
                    icon="el-icon-s-promotion"
                    type="primary"
                    @click="submit"
                    :loading="processing"
                >Êèê‰∫§Ââë‰∏âÂ∞èÂÜå</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import {
    __Root,
    __postType,
    __otherType,
} from "@jx3box/jx3box-common/data/jx3box.json";
import Tinymce from "@jx3box/jx3box-editor/src/Tinymce";
import CollectionPublic from "@jx3box/jx3box-editor/service/enum/CollectionPublic";
import header from "@/components/publish_header.vue";
import publish_banner from "@/components/publish_banner.vue";
import draggable from "vuedraggable";

// Êú¨Âú∞‰æùËµñ
import {
    get_legal_tags,
    get_collection,
    submit_collection,
} from "../service/collection";
import { get_posts_by_type } from "../service/post";

const qs = require("qs");
const lodash = require("lodash");

export default {
    name: "item",
    props: [],
    data() {
        // ÊñáÁ´†Á±ªÂûãÂä†ËΩΩ
        let source_types = Object.assign(__postType, __otherType);
        source_types["custom"] = "Ëá™ÂÆö‰πâ";
        delete source_types["cj"];

        return {
            source_types: source_types,
            public: CollectionPublic,
            // Ââë‰∏âÂ∞èÂÜå‰∏ª‰Ωì
            collection: {
                id: "",
                title: "",
                public: 1,
                image: "",
                description: "",
                tags: [],
                posts: [],
            },
            tag: "",
            legal_tags: null,
            show_description: false,
            processing: false,
        };
    },
    mounted() {
        if (!this.collection.posts.length) this.add_posts_item();

        if (this.$route.params.collection_id) {
            get_collection(this.$route.params.collection_id).then((res) => {
                res = res.data;
                if (res.code === 200) {
                    let collection = res.data.collection;
                    if (collection) {
                        for (let i in collection.posts) {
                            let item = collection.posts[i];
                            collection.posts[i].posts =
                                item.type === "custom"
                                    ? null
                                    : [{ id: item.id, title: item.title }];
                        }
                        this.collection = collection;
                    } else {
                        this.$message({
                            message: "ËØ•Ââë‰∏âÂ∞èÂÜåÂ∑≤Ë¢´Âà†Èô§ÊàñÊó†ÊùÉÈôêËÆøÈóÆ",
                            type: "warning",
                        });
                    }
                }
            });
        }
    },
    methods: {
        // ÂêàÊ≥ïÊ†áÁ≠æÊêúÁ¥¢‰ªÖËØ∑Ê±ÇÊï∞ÊçÆ‰∏ÄÊ¨°
        tags_search(query, cb) {
            if (this.legal_tags === null) {
                get_legal_tags().then((res) => {
                    res = res.data;
                    this.legal_tags = res.code === 200 ? res.data.tags : [];
                    cb(this.tags_filters(query));
                });
            } else cb(this.tags_filters(query));
        },
        tags_filters(query) {
            let output = [];
            for (let tag of this.legal_tags) {
                if (tag.split(query).length > 1) output.push({ value: tag });
            }
            return output;
        },
        // Â∏ñÂ≠êÊ†áÈ¢òÂ°´ÂÖÖ
        title_fill(post_id, item) {
            let post = item.posts[post_id];
            item.title = post && post.title ? post.title : "";
        },
        add_posts_item() {
            this.collection.posts.push({
                title: "",
                type: "",
                id: "",
                url: "",
                posts: null,
            });
        },
        search_handle(queryString, item) {
            if (queryString === null) item.id = queryString = "";
            get_posts_by_type(item.type, {
                public: 1,
                keyword: queryString,
            }).then((res) => {
                res = res.data;
                if (res.code === 200) {
                    item.posts = res.data.posts;
                }
            });
        },
        submit: function () {
            this.$confirm("Á°ÆÂÆöÊèê‰∫§Ââë‰∏âÂ∞èÂÜå‰ø°ÊÅØÔºü", "ÊèêÁ§∫", {
                type: "info",
            }).then(() => {
                // Ê†áÈ¢òÈïøÂ∫¶ÈôêÂà∂
                // this.collection.title = this.collection.title.slice(0,40);

                let collection = JSON.parse(JSON.stringify(this.collection));

                if (!collection.posts.length) {
                    this.$message({
                        message: "Ë¶ÅÊ∑ªÂä†Ââë‰∏âÂ∞èÂÜåÂÜÖÊñáÁ´†Âì¶",
                        type: "warning",
                    });
                    return;
                }

                // ÂéªÈô§Â§ö‰ΩôÂ≠óÊÆµ
                for (let i in collection.posts)
                    delete collection.posts[i].posts;
                collection.posts = JSON.stringify(collection.posts);

                this.processing = true;
                submit_collection(collection)
                    .then((data) => {
                        data = data.data;
                        if (data.code === 200) {
                            this.$message({message: data.message, type: "success"});
                            setTimeout(() => {
                                this.$router.push({name: 'bucket', params: {type: 'collection'}})
                            }, 500);
                        } else {
                            this.$message({
                                message: `${data.message}`,
                                type: "warning",
                            });
                        }
                    })
                    .finally(() => {
                        this.processing = false;
                    });
            });
        },
    },
    components: {
        Tinymce,
        draggable,
        "publish-header": header,
        "publish-banner": publish_banner,
    },
};
</script>

<style scoped lang="less">
@import "../assets/css/collection.less";
